.contact_container {
    width: 100%;
    display: flex;
    background-color: #fff;
}

.contact_description {
    width: 50%;
    padding: 50px;
}

.contact_form_container {
    width: 50%;
    background-color: rgb(75,163,160);
    padding: 80px;
    position: relative;
    &::after {
        content: '';
        position: absolute;
        width: 60px;
        height: 60px;
        background-color: rgb(75,163,160);
        transform: rotate(45deg);
        top: 80px;
        left: -30px;
        z-index: 899;
    }
}

.contact_description_head {
    font-size: 30px;
    font-weight: 800;
    margin-bottom: 40px;
}

.contact_description_title {
    font-size: 24px;
    margin-bottom: 40px;
}

.our-contacts {
    display: flex;
    flex-direction: column;
    &-item {
        display: flex;
        margin-bottom: 50px;
        max-width: 200px;
        line-height: 2;
        align-items: center;
        img {
            max-width: 40px;
            width: 100%;
            margin-right: 20px;
        }
    }
}

.contact_form {
    display: flex;
    flex-direction: column;
    &_input-container {
        width: 100%;
        display: flex;
        border: 1px solid #fff;
        border-radius: 5px;
        padding: 15px;
        color: #fff;
        background: transparent;
        margin-bottom: 20px;
        text-transform: uppercase;
        &::placeholder {
            color: #fff;
        }
        img {
            max-width: 30px;
            width: 100%;
            filter: invert(1);
            margin-right: 20px;
        }
        .contact_form_input {
            padding: 10px;
            background-color: transparent;
            font-size: 18px;
            text-transform: uppercase;
            color: #fff;
            border: none;
            width: 100%;
            &::placeholder {
                color: #fff;
            }
        }
    }
    .contact_textarea {
        height: 250px;
    }

    .contact_form_button {
        align-self: center;
        display: flex;
        max-width: 380px;
        border-radius: 50px;
        align-items: center;
        cursor: pointer;
        transition: all ease .3s;
        justify-content: center;
        span {
            margin-right: 20px;
            position: relative;
            transition: all ease .3s;
            right: 0;
        }
        img {
            filter: invert(1) ;
            position: relative;
            transition: all ease .3s;
            left: 0;
        }
        &:hover {
            span {
                right: 20px;
            }
            img {
                left: 20px;
            }
        }
    }
}

@media screen and (max-width: 880px) {
    .contact_container {
        flex-direction: column;
    }
    .contact_description, .contact_form_container {
        width: 100%;
    }

    .contact_form_container::after {
        top: -30px;
        left: 60px; 
    }
}

@media screen and (max-width: 420px) { 
    .contact_form_container {
        padding: 35px;
    }
    
    .contact_form {
        position: relative;
        z-index: 900;
    }
}

