.forum__new-message {
    display: flex;
    background-color: #fff;
    padding: 10px;
}


.forum__new-message {
    &-main {
        display: flex;
        flex-direction: column;
        padding: 15px;
        width: 100%;
    }
    &-title {
        padding: 20px;
        width: 100%;
        background-color: #F4F5F9;
        color: #999;
        box-sizing: border-box;
        margin-bottom: 20px;
        border: none;
        border-radius: 5px;
    }

    &-select {
        width: 200px;
        height: 40px;
        margin-bottom: 20px;
    }

    &-textarea {
        resize: none;
        width: 100%;
        padding: 15px;
        background-color: #F4F5F9;
        color: #999;
        box-sizing: border-box;
        height: 200px;
        margin-bottom: 20px;
        border: none;
        border-radius: 5px;
    }

    &-button {
        text-transform: uppercase;
        padding: 20px;
        background-color: rgb(75,163,160);
        color: #fff;
        border-radius: 10px;
        align-self: flex-end;
        cursor: pointer;
        transition: all ease .3s;
        &:hover {
            box-shadow: 0 0 10px rgba(0,0,0,.3);
        }
    }
    margin-bottom: 20px;
}
