.wrapper.__forum {
    display: flex;
    justify-content: space-around;
    align-items: center;
   
}

.forum__top {
    width: 100%;
    padding: 10px 0; 
    background-color: #fff;
    margin-bottom: 30px;
}



.forum__top_category {
    padding: 10px;
    border-radius: none;
    border: none;
    background-color: #fff;
    height: 30px;
    width: 150px;
    font-size: 18px;
    font-weight: 700;
}

.forum__top_search {
    display: flex;
    align-items: center;
    border-radius: 10px;
    overflow: hidden;
}

.forum_search_field {
    padding: 15px;
    border: none;
    background-color: #F4F5F9;
    color: #999;
}


.forum_search_button {
    background-color: rgb(75,163,160);
    padding: 11px;
    cursor: pointer;
    img {
        max-width: 25px;
        display: flex;
        align-items: center;
        justify-content: center;

    }
}