.login-form {
    display: flex;
    flex-direction: column;
}

.login-label {
    margin-bottom: 10px;
}
.login-input {
    padding: 5px;
    border: 1px solid rgba(0,0,0,.6);
    margin-bottom: 10px;
}

.login-submit {
    max-width: 200px;
    padding: 10px;
    background-color: rgb(75,163,160);
    cursor: pointer;
    margin-bottom: 10px;
    color: #fff;
}
.login-input {
    border: 1px solid rgba(0,0,0,.4);
    padding: 10px;
    border-radius: 5px;
}

.login-submit {
    border: none;
    border-radius: 10px;
    padding: 15px;
}

.form-title {
    font-size: 28px;
}