
.forum_sidebar-category {
    font-size: 14px;
    margin-bottom: 20px;
}

.forum__main_sidebar {
    width: 20%;
}

.forum__main_sidebar-item {
    background-color: #fff;
    padding: 20px;
    cursor: pointer;

}

.forum_sidebar-title {
    font-size: 16px;
    font-weight: 400;
    position: relative;
    width: 100%;
    box-sizing: border-box;
    &::after {
        content: "";
        height: 1px;
        position: absolute;
        width: 140%;
        bottom: -40%;
        left: -20%;
        background-color: #F4F5F9;
    }
    margin-bottom: 20px;
}