.footer-wrapper {
    max-width: 1600px;
    width: 100%;
    margin: 0 auto;
    padding: 30px;
    box-sizing: border-box;
}

.footer__top-desc {
    max-width: 500px;
}

.footer {
    width: 100%;
    background-color: #24262B;
    color: #fff;
    margin-top: 50px;
    position: absolute;
    bottom: 0;
    left: 0;
}

.footer__top {
    display: flex;
    justify-content: space-around;
    &-desc {
        display: flex;
        flex-direction: column;
        margin-right: 50px;
    }
    &-title {
        font-size: 20px;
        text-transform: uppercase;
        margin-bottom: 15px;
        font-weight: 700;
        color: #fff;
    }
    &-text {
        font-size: 16px;
        line-height: 1.5;
        margin-bottom: 20px;
    }
}
.footer__top-social-title {
    margin-bottom: 20px;
    font-size: 20px;
    font-weight: 700;
    color: #fff;
    text-transform: uppercase;
}

.footer__top-social_icons {
    display: flex;
    img {
        cursor: pointer;
        transition: all ease .3s;
        &:hover {
            filter: invert(1);
        }
        margin-right: 10px;
        width: 30px;
    }
}
