
.contact-form {
    display: flex;
    flex-direction: column;
    max-width: 460px;
    width: 100%;
}

.form {
    &__title {
        font-size: 20px;
        margin-bottom: 20px;
        font-weight: 700;
        color: #fff;
        text-transform: uppercase;
    }
    &__container {
        display: flex;
        flex-direction: column;
    }
    &__top {
        display: flex;
        margin-bottom: 20px;
    }
    &__input {
        margin-right: 10px;
        padding: 5px;
        width: 100%;
        font-size: 18px;
        padding: 10px;
        background-color: #32343B;
        width: 100%;
        &:last-child {
            margin-right: 0;
        }
        border: 1px solid #333;
    }
    &__textarea {
        resize: none;
        margin-bottom: 20px;
        padding: 5px;
        font-size: 18px;
        height: 100px;
        background-color: #32343B;
        border: 1px solid #333;
        width: 100%;
    }
    &__button {
        text-transform: uppercase;
        padding: 10px;
        background-color: #4ba3a0;
        border: none;
        max-width: 190px;
        align-self: flex-end;
        color: #fff;
        font-weight: 700;
        cursor: pointer;
    }
}
