.help_form_form {
    background-color: #fff;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 400px;
    box-shadow: 0 0 10px rgba(0,0,0,.4);
   
    &.hide {
        display: none;
        
    }
}


.checkbox {
    visibility: hidden;
    color: #000;
}

.choise_inv_or_not {
    font-size: 24px;
    color: #000;
    cursor: pointer;
    margin-bottom: 40px;
}

.choise_title {
    font-size: 30px;
    color: rgb(75,163,160);
    margin-bottom: 100px;
}
.request_form_title {
    font-size: 34px;
    margin-bottom: 30px;
    font-weight: 700;
    color: rgb(75,163,160);
  
}


.request_district {
    align-self: flex-start;
    padding: 10px;
    margin: 20px 0;
    margin-left: 20px;
}

.help_form_form {
    display: flex;
    flex-direction: column;
    z-index: 10;
    position: relative;
}

.help_form_bg {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
filter: grayscale(.7)
}

.request_input {
    border: 1px solid rgba(0,0,0,.4);
    padding: 10px;
    border-radius: 10px;
    max-width: 500px;
    margin-bottom: 20px;
    width: 100%;

    &.textarea {
        height: 150px;
    }
}
.request_checkBox {
    display: flex;
    margin-bottom: 20px;
    .block {
        margin-right: 40px;
        color: #000;
        input {
            margin-right: 10px;
        }
    }
}

.request_btn {
    padding: 20px;
    color: #fff;
    background-color: rgb(75,163,160);
    border: none;
    align-self: center;
    max-width: 250px;
    width: 100%;
    border-radius: 40px;
    font-size: 20px;
    font-weight: 700;
    cursor: pointer;
}