
.preloader {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #fff;
  opacity: 0;
  transition: all ease .5s;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: -1;
  &.active {
    opacity: 1;
    z-index: 1001;
  }
}
.lds-facebook {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: #4BA3A0;
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.lds-facebook div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}
.lds-facebook div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}
.lds-facebook div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}
@keyframes lds-facebook {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}


.map {
  background-color: gold;
  width: 100%;
  height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  & + div {
    position: relative; 
  }
  position: relative; 
  margin-bottom: 40px;
}

