$orange: #ff8c61;
$green: #32cd32;

.navItem {
    padding-bottom: 20px;
    position: relative;
    margin-right: 35px;
    color: #000;
    text-decoration: none;
    font-size: 20px;
    position: relative;
    padding-bottom: 20px;
    transition: all ease .3s;
    cursor: pointer;
}

.nav_href {
    color: #000;
}


header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 20px;
    padding-bottom: 0;
    box-shadow:  0 4px 20px -5px gray;
    z-index: 1000;
    background-color: #fff;
}

.logo {
    font-size: 30px;
    color: rgb(75,163,160);
    margin-right: 100px;
    padding-bottom: 10px;
    text-decoration: none;
}

.navBar {
    display: flex;
    align-items: center;
}

.flex {
    display: flex;
    align-items: center;
    width: 100%;
}

.navItem::after {
    content: '';
    width: 20px;
    height: 5px;
    background-color: rgb(75,163,160);
    position: absolute;
    bottom: 0;
    left: 0;
    transition: all ease .3s;
}

.navItem:hover::after {
    width: 100%;
}



.navItem:hover a {
    color: rgb(75,163,160) !important;
}

.enter {
    padding-right: 40px;
    display: flex;
    align-items: center;
    margin-bottom: 6px;
    cursor: pointer;
    position: relative;
    svg {
        color: rgb(75,163,160);
        position: absolute;
        left: -40px;
        transition: all ease 1.5s;
    }
   &:hover {
       
        svg {
            left: 120%;
        }
   }
}

.enter__text {
    margin-left: 10;
    color:#333;
}

.menu-container {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.burger_menu {
    display: none;
    padding: 10px;
    background-color: #4BA3A0;
    cursor: pointer;
    .burger-img {
        transition: transform ease .5s;
        width: 50px;
        filter: invert(1);
        transform: rotate(180deg)
    }
}






@media screen and (max-width: 1100px) {
    .navItem {
        margin-right: 27px;
        font-size: 12px;
    }
    .navBar {
        padding-top: 8px;
    }
    .enter {
        padding-right: 20px;
    }
}


@media screen and (max-width: 880px) {
    .menu-container {
        position: fixed;
        overflow: hidden;
        transition: all ease .5s;
        top: 70px;
        left: 100%;
        z-index: 100;
        width: 100vw;
        height: 95vh;
        background-color:rgba(75,163,160, .95);
        padding: 50px;
        flex-direction: column;
        align-items: flex-end;
        .login-wrapper {
            padding: 20px 20px 20px 50px;
            align-self: flex-end;
            border: 1px solid #fff;
            background-color: #333;
        }
        .enter {
            font-size: 24px;
            .enter__text {
                color: #fff;
                font-weight: 800;
            }
            padding-right: 0;
        }
        .navBar {
            flex-direction: column;
    
            align-items: flex-end;

            .navItem {
                font-size: 24px;
                margin-bottom: 40px;
                margin-right: 0;
                width: 100%;
                text-align: right;
                &:hover a {
                    color: #333 !important;
                } 
                a {
                    &.active {
                        color: #333;
                    }
                    color: #fff;
                    font-weight: 800;
                }
                &::after {
                   // display: none;
                   width: 100%;
                   background-color: rgba(51,51,51, 0.7);
                }
            }
        }
        .MuiSvgIcon-root.MuiSvgIcon-fontSizeLarge {
            color: #fff;
        }
        &.active {
            left: 0;
        }
    }

    .burger_menu {
        display: block;
        &.active {
            .burger-img {
                transform: rotate(0deg);
            }
        }
    }
    .header {
        padding: 0 0  0 20px;
    }
}