

.wrapper.__padding {
    padding:  0 50px;
}



.forum__main {
    display: flex;
    margin-top: 50px;
}

.forum__main_left {
    margin-right: 40px;
    display: flex;
    flex-direction: column;
    width: 65%;
}



.forum__avatar {
    padding: 15px;
    position: relative;
    &::after {
        content: "";
        height: 1px;
        position: absolute;
        width: 90%;
        top: 15%;
        left: 50%;
        transform: translateX(-50%);
        background-color: #F4F5F9;
    }

    &._c {
        &::after {
            top: 60%;
       
        }
    }
    &-img {
        border-radius: 50%;
    }
}



.forum__comments {
    display: flex;
    flex-direction: column;
}






@media screen and (max-width: 1100px) {
    .forum__main_left {
        width: 100%;
    }
    
    .forum__main_sidebar {
        display: none;
    }
}