.filterbar-container {
    display: flex;
    justify-content: space-between;
    padding: 20px 50px;
    background-color: #f4f4f4;
}

.select-place {
    padding: 10px;
    option {
        padding: 10px;
    }
}


label.ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
    border: 1px solid rgb(75,163,160)
}

.ant-radio-button-wrapper-checked {
    color: #4ba3a0 !important;

    border-color: #4ba3a0 !important;
    -webkit-box-shadow: -1px 0 0 0 #4ba3a0 !important;
    box-shadow: 0 0 1px #4ba3a0 !important;
}

.ant-radio-button-wrapper:hover {
    color: #4ba3a0;
}