.help-container {
    width: 100%;
    display: flex;
    position: relative;
    box-sizing: border-box;
}

.help_description {
    width: 50%;
    background-color: rgba(	75,	163, 160,.9);
    padding: 250px 100px;
    color: #fff;
}

.help_title {
    color: #fff;
    font-size: 34px;
    margin-bottom: 30px;
    font-weight: 700;
}

.help_form_container {
    width: 50%;
    padding: 40px 100px;
    position: relative;
}

.help-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    box-sizing: border-box;
}

.help_form_container>div {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}
.help_form_form {
    flex: 1;
    height: 100%;
    align-items: center;
    justify-content: center;
}

.back-arrow {
    position: absolute;
    top: 30px;
    left: 30px;
    transform: rotate(180deg);
    width: 30px;
    cursor: pointer;
}


@media screen and (max-width: 1100px) {
    .help_form_container, .help_description {
        width: 100%;
    }
    .help_description {
        padding: 100px;
    }
    .help-container {
        flex-direction: column;
    }
}

@media screen and (max-width: 880px) {

    .help_form_container, .help_description {
        padding: 40px;
    }

    .request_form_title {
        margin-left: 50px;
    }
}