
.forum__comment {
    display: flex;
    padding: 20px;
    margin-bottom: 20px;
    &-main {
        display: flex;
        flex-direction: column;
        padding: 10px;
        width: 100%;
        box-sizing: border-box;
    }
    &-title {
        font-size: 20px;
        font-weight: 700;
        margin-bottom: 20px;
    }

    &-text {
        line-height: 1.5;
        margin-bottom: 20px;
        width: 100%;
    }
    &-date {
        color: #000;
        align-self: flex-end;
    }
    background-color: #fff;
}