.invo_logotip{
    width: 100%;
    max-width: 150px;
}
.logo_text{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    align-items: center;
    padding: 0 20px;
}
.content {
    background-color: #fff;
}
.container{
    width: 100%;
    max-width: 1600px;
    background-color: #fff;
    margin: 0 auto;
}
.contetnt_introdution{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.content_header{
    margin-bottom: 20px;
}
.content_disc{
    margin-bottom: 20px;
    font-size: 20px;
    text-align: justify;
    font-style: italic;
}
.goals_header{
    text-align: center;
    margin: 40px 0 20px 0;
}
.content_team_img{
    box-shadow: -2px 2px 15px 0;
    width: 100%;
}
.content_each_team_member{
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fff;
}
.content_team_membs{
    margin-bottom: 20px;
    font-size: 24px;
    padding-top: 10px;
}
.team-block{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.team_member{
    width: 100%;
    filter: grayscale(100%);

    transition: filter .3s linear;
    margin-bottom: 20px;
}
.team_member:hover{
    filter: grayscale(0%);
    filter: blur(3px);
}
.team_member_info{
    z-index: 2999;
    width: 100%;
    opacity: 0;
    position: absolute;
    border: 1px solid rgba(161, 161, 161, 0.582);
    text-align: center;
    border-radius: 10px;
    background-color: rgba(75,163,160,.8);
    padding: 10px;
    transition: all .3s linear;
    top:50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 18px;

    transition: all .3s linear;
}
.team_member_total:hover .team_member_info{
    opacity: 1;
}

.team_member_info:hover + .team_member{
    filter: grayscale(0);
    filter: blur(3px);
}

.team_member_total {
    width: 20%;
    position: relative;
    cursor: pointer;
}
.project_aims{
    margin-top: 30px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}
.proj_goal{
    margin-bottom: 20px;
    cursor: pointer;
    width: 30%;
    background-color: rgb(75,163,160);
    box-shadow: 0 0 10px 0;
    border-radius: 5px;
    min-height: 65px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 30px 0 30px 0;
    flex-wrap: wrap;
    justify-content: space-between;
    border: 1px solid rgba(129, 129, 129, 0.5);
    color: #fff;
}
.goal_lofo{
    max-width: 48px;
    width: 20%;
    padding-left: 10px;
    filter: invert(1);
}
.goal_disc{
    width: 80%;
    padding-right: 10px;
    text-align: justify;
}
.time_spend{
    //background: url(./assets/aaaaaBG.jpg) no-repeat, center;
    background-size: cover;
    color: #fff;
    margin-top: 40px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    padding: 80px 0 80px 0;
}
.intrest_inf{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.number{
    margin-bottom: 20px;
    font-size: 42px;
    font-family: Raleway, sans-serif;
    color: #000;

}
.numb_dics{
    font-style: italic;
    font-size: 20px;
    color: #000;
}
.mobile_application{
    width: 100%;
    padding-top: 30px;
    padding-bottom: 30px;
    margin: 40px auto 0;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    background-color: #4BA3A0;
    opacity: .9;
    color: #fff;
}
.mob_app_wrapp{
    width: 1200px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
.mob_app{
    border-radius: 10px;
    height: 350px;
    box-shadow: 0 0 10px rgb(122, 121, 133);
}
.mob_app_text{
    width: 70%;
    display: flex;
    flex-direction: column;
    justify-content: center;

}
.mobile_app_header{
    margin-bottom: 40px;
    font-weight: bold;
    font-size: 24px;
}
.mob_app_disc{
    color: #fff;
}
.take_part{
    margin-top: 40px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.take_part_header{
    margin-top: 20px;
    margin-bottom: 20px;
}
.participation_block{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
    flex-wrap: wrap;
}
.reward_block{
    height: 380px; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 45%;
    box-shadow: 0 0 10px 0;
    margin-bottom: 50px;
}
.reward_img{
    width: 100%;
    height: 300px;
}
.take_part_disc{
    text-align: center;
    width: 60%;
    font-size: 18px;
    font-weight: bold;
    padding: 0 10px 0 10px;
}


@media screen and (max-width: 880px) {
    .take_part_disc {
        width: 96%;
    }
    .participation_block {
        flex-direction: column;
    }

    .proj_goal {
        height: 150px;
        width: 45%;
    }

    .team_member_total {
        width: 35%;
        margin-right: 40px;
    }
    .team-block {
        justify-content: center;
    }
} 

@media screen and (max-width: 680px) {
    .mob_app {
        display: none;
    }

    .reward_block {
        width: 85%;
    }
    .time_spend {
        flex-wrap: wrap ;
    }
    .intrest_inf {
        width: 45%;
        margin-bottom: 20px;
    }
    .numb_dics {
        text-align: center;
    }
}

@media screen and (max-width: 420px) { 
    .team_member_total {
        width: 75%;
        margin-right: 0;
    }
    .proj_goal {
        width: 75%;
    }
    .intrest_inf {
        width: 75%;

    }

    .logo_text {
        flex-wrap: wrap;
        justify-content: center;
    }

    .reward_img {
        height: 250px;
    }
}

@media screen and (max-width: 350px){
    .proj_goal {
        width: 90%;
    }
}