.map-wrap {
    position: relative;
    max-height: 400px;
    height: 400px;
}


.wrapper {
    max-width: 1600px;
    width: 100%;
    margin: 0 auto;
}

.cards_container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap
}

.pharm-card {
    opacity: 0;
    position: relative;
    top: 100px;
    transition: all ease .8s;
    &.active {
        opacity: 1;
        top: 0;
    }
}


@media screen and (max-width: 880px) {
    .carddesc {
        left: -130%;
        width: 100%;
        overflow-y: scroll;
        padding: 10px;
        padding-top: 100px;
    }

    .carddesc__top {
        flex-wrap: wrap;
    }

    .carddesc__top-img {
        margin-bottom: 20px;
        margin-right: 0;
    }

    .filterbar-container {
        flex-wrap: wrap;
    }

    .ant-select-lg.ant-select.ant-select-enabled {
        margin-top: 20px;
    }
}


@media screen and (max-width: 690px) {
    .pharm-card {
        width: 55%;
    }
    .contact-form.form {
        display: none;
    }
}

@media screen and (max-width: 420px) {
    .pharm-card {
        width: 80%;
    }

    .wrapper.__forum {
        flex-direction: column-reverse;
        
    }

   
}


